






















































import Vue from "vue";
import router from "../router";

export default Vue.extend({
  name: "Jummah",
  data: () => ({
    postcode: null
  }),
  methods: {
    changeRoute() {
      console.log("Postcode: ", this.postcode);
      router.push({
        name: "Mosques",
        query: { postcode: this.postcode, radius: "1" }
      });
    }
  }
});
